body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  overflow-y: overlay;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Login {
  height: 100vh;
}

.login-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 10vmin;
}
.Register {
    height: 100vh;
  }
  
.register-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 10vmin;
}
.NavItem > a {
    display: -webkit-flex;
    display: flex;
    text-decoration: none;
    color: #555;
    transition: 0.2s ease;
}

.item {
    margin-left: 5px !important;
    display: none !important;
}

.NavItem > a.active,
.NavItem > a:hover {
    color: #3f51b5;
}

.active .item:after {
    left: 0;       
    width: 100%; 
}

.item:after {
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: relative;
    background: #3f51b5;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;      
}


@media only screen and (min-width: 768px) {
    .item {
        display: inline-block !important;
    }

    .link-wrapper:hover .item:after {
        width: 100% !important; 
        left: 0;       
    }
}

.image {
  height: 25vh;
  margin: 2vh;
}

.body-wrapper {
  height: 21vh;
  width: 100%;
  overflow: hidden;
  margin: 2vh;
  word-wrap: break-word;
}
.body-wrapper-full {
    width: 100%;
    word-wrap: break-word;
    padding-top: 1vh;
    padding-bottom: 1vh;
}

.App {
  background: linear-gradient(90deg, rgba(204,215,216,1) 0%, rgba(255,255,255,1) 19%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 81%, rgba(204,215,216,1) 100%);
  min-height: 100vh;
}

